import {
  Box, Text,
  Image,
  HStack,
  Link,
  IconButton,
  useDisclosure,
  Stack,
  Spacer,
} from '@chakra-ui/react';
import { HamburgerIcon, CloseIcon } from '@chakra-ui/icons';

const Links = ['Home', 'Aktuelles', 'Leistungen', 'Kontakt', 'Karriere'];

const NavLink = ({ children }) => (
  <Link
    px={2}
    py={1}
    rounded={'md'}
    _hover={{
      textDecoration: 'none',

    }}
    href={'#'}>
    {children}
  </Link>
);

export default function NefinoNavbar() {
  const { isOpen, onOpen, onClose } = useDisclosure();

  return (
    <>
      <Box px={4}>
        <HStack h={16} >
          <Link p="2.5" ml="6">
            <Image
              width="153px"
              height="40px"
              //src="https://www.nefino.de/wp-content/uploads/2021/02/Nefino_logo.png"
              src="https://www.nefino.de/wp-content/uploads/2021/02/Nefino_white.svg"
            />
          </Link>
          <Spacer />

          <HStack spacing={[4, 5, 6]} alignItems={'center'}>

            <HStack
              as={'nav'}
              spacing={[1, 2, 3]}
              display={{ base: 'none', md: 'flex' }}>
              {Links.map((link) => (
                <NavLink key={link}>{link}</NavLink>
              ))}
              <NavLink>
                <Image
                  width="2vw"
                  height="14px"
                  src="data:image/png;base64,"
                />
              </NavLink>
              <NavLink>
                <Image
                  width="2vw"
                  height="14px"
                  src="data:image/png;base64,"
                />
              </NavLink>
            </HStack>

          </HStack>

          <IconButton
            bg="transparent"
            size={'md'}
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={'Open Menu'}
            display={{ md: 'none' }}
            onClick={isOpen ? onClose : onOpen}
          />
          <Text style={{ marginInlineStart: '0' }} display={{ md: 'none' }}>Menü</Text>

        </HStack>

        {isOpen ? (
          <Box pb={4} display={{ md: 'none' }} bg="white" color="blue.700" position="absolute" left="0" width="100%" style={{ zIndex: "1" }}>
            <Stack as={'nav'} spacing={4}>
              {Links.map((link) => (
                <NavLink key={link}>{link}</NavLink>
              ))}
              <NavLink>
                <Image
                  width="5vw"
                  height="14px"
                  src="data:image/png;base64,"
                />
              </NavLink>
              <NavLink>
                <Image
                  width="5vw"
                  height="14px"
                  src="data:image/png;base64,"
                />
              </NavLink>
            </Stack>
          </Box>
        ) : null}
      </Box>

    </>
  );
}